<template>
	<planehotel :father='plhotelnew'></planehotel>
</template>
<script>
	import {duoriyou} from '../../data/shopping/duoriyou.js'
	import planehotel from './shoppingmodel'
	export default{
		components:{
			planehotel
		},
		mounted(){
            this.fetchData();
		},
		data(){
			return {
				plhotelnew:[]
			}
		},
		methods:{
			fetchData(){
                this.plhotelnew =  JSON.parse(JSON.stringify(duoriyou));
           	}
		}
	}
</script>
	
<style>
</style>


