	answerthreelevel: 7,
	answerthreecontent: [{
		c: '不论是出国还是在国内旅行  ，现在坐飞机出行普遍又普通了。办理登机手续的时候，工作人员常会问：你愿意要靠窗(window)还是靠走道(aisle)的坐位？有些开通了自动办票(self-check-in) 的航空公司的航班，乘客可以提前办理登机手续并自选座位；还有的航空公司在购票的时候就要选择座位(有些座位要另付费的)。一些朋友遇到这些情况时有些萌，不知该怎么办，也不太知道该选哪个座位好。乘飞机选座位，有人喜欢靠窗，有人喜欢靠走道，个人的情况、喜好、旅行习惯不同，对座位的要求也不一样。这里，根据自己多年的旅行经验给大家简单介绍，作个参考。<br>阿提哈德航空(新浪旅游配图)　　现在民用航空飞机多是较大型的，经济舱一般每排十个座位，靠两边的座位都是三个，中间的座位每组四个。多数乘客一般要么选择靠窗，要么选择靠走道，很少有人愿意坐中间的座。那么它们各自都有哪些优缺点呢？<br>靠窗的座位优点很明显，座位一边没人，有舷窗可以眺望外面，困了还可以斜靠着眯一觉，别人干什么不受打扰。缺点就是坐在里面，进出不方便。有在飞机上爱上厕所的人最好别选靠窗，呵呵。靠走道的座位的优点就是进出方便，也不打扰别人。还有个好处就是一边没人，现今的经济舱座位的 leg-room 太窄，靠走道的空间大，还可以把腿伸到座位外侧。当然，靠走道也有不便之处，里面座的人进出时，你老得起身，再碰上个爱上厕所的，这一路上就不得安宁了；另外机上的服务，不论是发餐饮、还是购物  ，都得从你头顶越过去，弄不好还可能洒身上。中间的座位一般人都尽量避免，夹在两边儿乘客之间，左右都不方便。<br>外国网友上传的最佳飞机座位示意图　　还有些一般人们不太了解的地方。比如，乘客可以选择紧急出口排的座位，这排座位的优点是腿部空间留得大(为方便紧急撤离)，尤其对那些个儿高、腿长的人比较合适。但是，这些座位航空公司要“看人”才给。不是歧视，按照航空安全规定，飞机紧急迫降需要打开紧急出口时，紧急情况一般都是由坐在这里的乘客打开。所以，航空公司一般安排一些年轻力壮、腿脚利索的人坐在这里，老弱病残孕等就免谈了。呵呵。还有人愿意坐第一排，优点是前面没有座位、空间大，进出方便。但是，第一排一般安排有抱婴儿的乘客，因为前面可以安放婴儿挂床。<br>除了以上考虑外，个人还愿意尽量选择飞机前部的座位，越往前越好。坐在前排，上下飞机快捷方便，餐饮上得快，特别对于那些飞机起飞后想尽快吃饭然后蒙头睡觉的乘客合适。另外，飞机发动机一般在飞机的中后部，坐在飞机前部，噪音小。<br>好了，就说这些。',
		p: 'https://b4-q.mafengwo.net/s9/M00/86/E6/wKgBs1fYqDGAB1InAAC5CWzvUlA65.jpeg?imageView2%2F2%2Fw%2F700%2Fh%2F1000%2Fq%2F90'
	}],
