	answertwogood: 90,
	answertwopinglun: 0,
	answerthreepic: 'https://a2-q.mafengwo.net/s10/M00/6B/F5/wKgBZ1kzfPOASZ2WAACgGKNVDYw15.jpeg?imageMogr2%2Fthumbnail%2F%2190x90r%2Fgravity%2FCenter%2Fcrop%2F%2190x90%2Fquality%2F90',
	answerthreename: '帕劳孙记别墅Tina',
	answerthreelevel: 12,
	answerthreecontent: [{
		c: '“亚洲50最佳餐厅”是由亚洲地区的300多名美食专家（由厨师或餐厅经营者、专业美食媒体评论员以及资深食客三类人群按照三分比例组成），按饮食习惯分为6大区域（中国大陆、中国港澳台、日韩、印度、中亚及次大陆、东南亚南部及东南亚北部），全匿名全保密投票产生的结果。<br><br>评选过程简示图：<br>今年新入围的曼谷餐厅Le Du排名在第37位，作为新晋选手在大赛上备受瞩目，早在它入围之前隐行家的美食侦探们就已经将它纳入了我们的secret table（隐秘餐桌），今天我们重点来扒一扒这匹生猛黑马。',
		p: 'https://p3-q.mafengwo.net/s10/M00/8C/93/wKgBZ1k3ynyAX0gKAARmwayqQAQ68.jpeg?imageView2%2F2%2Fw%2F700%2Fh%2F1000%2Fq%2F90'
	}, {
		c: '大写的学霸<br>Le Du 的主厨Ton大学学的专业是经济学，毕业后在银行工作了两个月就辞职去了美国烹饪学院（Culinary Institute of America）学习，半路出家却以当届最高分从学校毕业，然后开始了在纽约顶级米其林餐厅的实践工作，在取得足够的经验后再回到曼谷开始创业，现在年仅30岁已经拥有了四家餐厅。<br><br>Chef Ton:<br>2015年的时尚杂志 ELLEMAN 睿士选出了最能领导泰国的Top40 Men，入围的选手都是泰国商界精英，资深艺术家和当红艺能界小鲜肉，Ton作为唯一的厨师入围，杂志给他的称号是The new culinary force(新烹饪力量)。<br>学霸的餐桌<br>如果你以为学霸的餐桌就会循规蹈矩的话，那Le Du的菜单一定让你刷新对学霸的认知。这里的常驻菜单分为：冷（Cold）、海洋（From the Sea）、牧场（From the Ranch）和罪恶（Sin）四系列，每系列有三种选择供你探索。',
		p: 'https://c4-q.mafengwo.net/s10/M00/8C/C9/wKgBZ1k3yrSAP3r7AANrWjRGG0M90.jpeg?imageView2%2F2%2Fw%2F700%2Fh%2F1000%2Fq%2F90'
	}, {
		c: '让人印象深刻的甜点：猪血布丁，与泰国罗勒冰淇淋，淡盐焦糖，杏仁碎, 山莓果酱，一起呈上，猪血布丁口感丝滑并带有一丝巧克力的香味，但比之更馥郁柔滑，与其它意想不到的配料混合在一起不觉奇怪，而是给人一种本来就该这样的完美感觉：',
		p: 'https://c4-q.mafengwo.net/s10/M00/8C/D0/wKgBZ1k3yrqANxgNAAP8q59M-i069.jpeg?imageView2%2F2%2Fw%2F700%2Fh%2F1000%2Fq%2F90'
	}, {
		c: 'Le Du的厨房大部分都雇佣本地人，Ton认为让泰国的年轻厨师们树立良好的思考很重要，如果人们老是只做一样的事，不思进取的话，就不会有任何的创新，也许这就是为什么现在泰国年轻人不再想做厨师的原因。每个人对支持自己的产业和教育年轻人都有不能推卸的责任。',
		p: 'https://b3-q.mafengwo.net/s10/M00/8C/DF/wKgBZ1k3ys6AK0YXAAKzIQZNgz423.jpeg?imageView2%2F2%2Fw%2F700%2Fh%2F1000%2Fq%2F90'
	}],
