	title: '来一场悠闲的逛吃之旅，看尽...',
	pic: 'https://images.mafengwo.net/mobile/images/together/topNum-bg.jpg',
	name: 'snowswansagasolo北京',
	touxiang: 'https://n1-q.mafengwo.net/s10/M00/33/36/wKgBZ1khl7iAT0ypACd_eBfIBTQ799.png?imageMogr2%2Fthumbnail%2F%21120x120r%2Fgravity%2FCenter%2Fcrop%2F%21120x120%2Fquality%2F90',
	time: '2017-09-30',
	days: 9,
	id: 5,
	contect: '报名后可见',
	content: '西藏，一个多少人向往的天堂，一个能听见神说话的神秘地方，你是否也曾梦想过踏入这片人间净土？伫立在布达拉宫广场，仰望那雄伟、充满沧桑的布达拉宫。你是否梦想过站在5200米的高原，呼吸着稀薄的空气，静静的遥望那云雾中带着巨大雪冠的世界之巅珠穆朗玛？你是否也曾在梦中逃离过这喧嚣的大都市，来到西藏林芝那个静静的世外桃源，看到了雪域高原的圣湖、林海、雪山。。。上帝只给我们一次年轻的机会，梦想由心而生，不要让我们的梦想再一次被深埋，趁我们还年轻，背上你行囊，说走就走的行程，跟我出发吧！<br>领队介绍：<br>户外名：中华，生在山东，长在山东。因为户外想过一种自己想要的生活，改行做摄影师（国家地理签约摄影师）、兼职领队（戈尔特斯乐行俱乐部认证领队、探路者绿野认证领队）。幸运的把自己的爱好当成自己的工作。非常热衷于徒步，对于我来说爬山涉水不是一个抽象的概念，那是用自己的双脚一步一步真实的行走。八次进藏拍片，五年多时间我徒步过不少地方，比如说西藏墨脱、阿里、珠峰、纳木错、青海湖、海坨、泰山、五台山、灵山等等。我真心希望每一个朋友能在我的带领下能用自己的双脚去感受这片土地的美丽与神奇。<br>【强烈建议】:<br>进藏火车票很难买。很多队友问火车进藏不容易高反，飞机容易高反。作为多次入藏的领队，实际上来说高反因人而异，每个人都会有，和飞机火车无关，减轻高反可提前一周服用红景天或高原安，是中草药无副作用。建议火车进藏，火车或飞机出藏。',
