	title: '藏，海花。在最靠近你的地方...',
	pic: 'https://images.mafengwo.net/mobile/images/together/topNum-bg.jpg',
	name: '梦游坦克长沙',
	touxiang: 'https://n2-q.mafengwo.net/s5/M00/D4/24/wKgB3FB_zz3h_6WaAAKw1dw_yig29.jpeg?imageMogr2%2Fthumbnail%2F%21120x120r%2Fgravity%2FCenter%2Fcrop%2F%21120x120%2Fquality%2F90',
	time: '2017-09-15',
	days: 9,
	id: 13,
	contect: '报名后可见',
	content: '去西藏的人，总是有各种各样的理由和动机。为山，为水，为情怀。当然，这些我也为。但我更是为了一个人。跟随他的脚步，我已经走过了很多地方。虽然是无意。但机缘巧合下却是发现，我们的脚印总是在不同的时空里重合着。念念不忘，必有回响。那些你走过的路，看过的云，我在真实世界里，替你走一走。看一看。听一听，风马旗里，群山回响。<br>大概行程是长沙或各地出发，9月16或17号拉萨集合。24、25日回程。9月中旬来西藏是因为有几率可以遇见薄雪，又避开了十一和暑假的高峰期。由于肯定不止只来一次西藏，所以这第一次的来，更多是来踩踩点。感受感受高原。适应适应海拔。了解了解当地的交通、物价、气候、节日、风俗等，为下几次走大环线，上珠峰大本营做前期准备。适应得好就走走周边几个圣湖。有合适的团队就出发去林芝一带。要求队员至少有过一次独自旅行的经历。有走过西北、西南或者徒步过户外的人优先。有摄影技术的优先。大个子劳动力优先~~基本要求，身体健康，无高原隐患诱发疾病的，有团队精神，吃苦耐劳的，能冷静面对各种突发状况的。女生不要有公主病。男生不要太直男癌。总归，能和谐相处，愉快旅行。有医生、摄影、部队运动员行业的优先。<br>本人女，坐标长沙，有过多次团队旅行和独自旅行经验。能自己照顾自己。已有两个行程确定的旅伴。也是马蜂窝集结的。<br>有意向者，请加QQ详聊。有问题请留言。见者必回。图片均取自蜂蜂游记。',
