	touxiang: 'https://p4-q.mafengwo.net/s9/M00/9C/B7/wKgBs1cDOreAKSSeAAwcT2LeOPY09.jpeg?imageMogr2%2Fthumbnail%2F%21120x120r%2Fgravity%2FCenter%2Fcrop%2F%21120x120%2Fquality%2F90',
	time: '2017-09-08',
	days: 9,
	id: 16,
	contect: '报名后可见',
	content: '在当地租车之后全程自驾深度游基本上概括了呼伦贝尔的所有景点，本人从广州出发，可以一同出发或者在海拉尔集中出发也可以。一有一女生确定，另外一个女生还在考虑当中，欢迎热爱摄影的一同出发拍摄最好的呼伦贝儿。由于摄影，会起早贪黑的出发，相对会比较辛苦点，请慎重。行程参考：D1:(海拉尔)  ----金帐汗--莫日格勒河--敖包山--(额尔古纳)  --根河湿地--敖鲁古雅--根河D2：根河--金河--牛耳河--阿龙山镇—满归D3：满归--(漠河)  -(北极)  村D4：北极村-漠河-满归-姊妹湖、伊克萨玛国家森林公园--奇乾D5： 奇乾--白鹿岛--莫尔道嘎森林公园—太平村—老鹰嘴—月亮泡--(临江)  D6： 临江—室韦-- 恩河—七卡—六卡—五卡—黑山头--呼伦湖--套娃广场--满洲里D7：满洲里--贝尔湖--东旗--西旗--七仙湖--玫瑰峰--(阿尔山)  公园D8：阿尔山公园（天池，石塘林，三潭峡，杜鹃湖，大峡谷， 驼峰岭天池）D9：阿尔山公园--红花尔基樟子松公园--巴彦呼硕--海拉尔  行程可以可以再商量，欢迎加微信咨询。',
